.header-container {
  width: 100%;
  height: 6rem;
  display: flex;
  justify-content: space-around;
  align-items: center;
  gap: 5rem;
  background-color: $fifth-color;
  border-radius: 0 0 0.5rem 0.5rem;
  box-shadow: $second-color 0px 50px 20px -40px;

  p {
    color: $white-color;
    font-size: 2.5rem;
  }

  &-links {
    display: flex;
    justify-content: center;
    gap: 1rem;

    a {
      text-decoration: none;
      color: $third-color;
      transition: 0.4s;
      font-size: 1.5rem;

      &:hover {
        color: $fourth-color;
      }
    }
  }
}
